.header {
  max-width: 880px;
  width: 100%;
  margin: 0 auto;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
  display: flex;
  justify-content: space-between;
  margin-top: 45px;
  margin-bottom: 40px;
}

@media screen and (max-width: 900px) {
  .header {
    max-width: 581px;
  }
}

@media screen and (max-width: 600px) {
  .header {
    margin-top: 28px;
    margin-bottom: 42px;
  }
}
